const Auth = {
    //
    register(username, password) {
        let user = new User()
        user.setUsername(username)
        user.setPassword(password)
        return new Promise((resolve, reject) => {
            user.signUp().then(loginedUser => {
                console.log('')
                resolve(loginedUser)
            }, error => {
                reject(error)
            })
        })
    },
    //
    login(username, password) {
        return new Promise((resolve, reject) => {
            User.logIn(username, password).then(loginedUser => {
                console.log('')
                resolve(loginedUser)
            }, error => {
                reject(error)
            })
        })
    },
    //
    logout() {
        User.logOut()
    },
    //
    getCurrentUser() {
        return User.current()
    }
}

//
const UpLoader = {
    // add
    add(file, filename) {
        //UpLoaderadd,filefilename
        //
        const item = new AV.Object("Image") //item
        const avFile = new AV.File(filename, file) //
        item.set('filename', filename) //
        item.set("owner", AV.User.current()) //
        item.set('url', avFile) //
        //
        return new Promise((resolve, reject) => {
            item.save().then((serverFile) => {
                console.log('')
                resolve(serverFile)
            }, (error) => {
                console.log('')
                alert(JSON.stringify(error))
                reject(error)
            })
        })
    },
    //
    //0 10
    find(page = 0, limit = 10) {
        const query = new AV.Query('Image') //
        query.include('owner')//owner
        query.limit(limit) //
        query.skip(page * limit) //
        query.descending('createdAt')//
        query.equalTo('owner', AV.User.current())//
        return new Promise((resolve, reject) => {
            query.find()
                .then(results => resolve(results))
                .catch(error => reject(error))
        })
    },
    delete(id){
      if(!id) throw new Error ('objectId')
      const image = AV.Object.createWithoutData('Image',id)
      return image.destroy()
    }

}

window.UpLoader = UpLoader

export {Auth, UpLoader};
