import React, {useEffect} from "react";
import logo from './logo.svg'
import {NavLink, useNavigate} from "react-router-dom";
import styled from 'styled-components'
import {Button} from "antd";
import {useStores} from "../stores";
import {observer} from "mobx-react";
import userStore from '../stores/user'


const Header = styled.header` //
  background: linear-gradient(90deg, rgba(42, 48, 113, 1) 3%, rgba(33, 9, 69, 1) 100%);
  padding: 10px 100px;
  display: flex;
  align-items: center;
  color: #fff;
`
const Logo = styled.img`
  height: 30px;
`

const StyledLink = styled(NavLink)`
  color: #fff;
  margin-left: 30px;

  &.active {
    color: #d3d6a5;
    font-weight: bold;
  }
`
const Login = styled.div`
  margin-left: auto;
`
const StyledButton = styled(Button)`
  margin-left: 10px;
`


const Component = observer(() => {
    //
    const {UserStore, AuthStore} = useStores() //UserStore
    const navigate = useNavigate()
    const handleLogout = () => {
        AuthStore.logout()
    }
    const handleLogin = () => {
        console.log('')
        navigate("/login")
    }
    const handleRegister = () => {
        console.log('')
        navigate("/register")
    }
    useEffect(()=>{
        userStore.pullUser() //
    },[])
    return (
        <Header>
            <Logo src={logo} alt=''/>
            <nav>
                <StyledLink to='/' activeclassname='active'></StyledLink>
                <StyledLink to='/history' activeclassname='active'></StyledLink>
                <StyledLink to='/about' activeclassname='active'></StyledLink>
            </nav>
            <Login>
                {/**/}
                {/*/*/}
                {UserStore.currentUser ? <>
                        {UserStore.currentUser.attributes.username} <StyledButton type="primary"
                                                                                  onClick={handleLogout}></StyledButton>
                    </> :
                    <>
                        <StyledButton type="primary" onClick={handleLogin}>
                            
                        </StyledButton>
                        <StyledButton type="primary" onClick={handleRegister}>
                            
                        </StyledButton>
                    </>
                }
            </Login>
        </Header>
    )
})

export default Component


